import Vue from 'vue';
import Router from 'vue-router';

//办公管理
import Gonggaoguanli from '@/components/Gonggaoguanli';
import Gonggao from '@/components/Gonggao';

Vue.use(Router);

let router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      component: Gonggaoguanli,
      name: '公告发起',
      menuShow: false,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/gonggao',
      component: Gonggao,
      name: '公告发起',
      menuShow: false,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/gonggaoguanli',
      component: Gonggaoguanli,
      name: '公告发起',
      menuShow: false,
      meta: {
        requiresAuth: true
      }
    },
  ]
});

export default router;



