<template>
  <div class="oa_main">
    <!--list-->
    <el-row class="gonggao">
      <div class="fasong">
        <el-button type="primary" @click="querenfasong">立即创建</el-button>
        <el-button type="primary" v-if="yulan==0" @click="querenyulan">预览</el-button>
        <el-button type="primary" v-if="yulan" @click="querenyulan">关闭预览</el-button>
        <el-button type="primary" @click="guanbi()">关闭</el-button>
      </div>
      <el-col :span="24">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="公告标题">
            <el-input v-model="form.biaoti" placeholder="请输入公告标题"></el-input>
          </el-form-item>
          <el-row></el-row>
          <el-form-item label="正文" class="editer">
            <vue-editor
              v-if="yulan==0"
              useCustomImageHandler
              @imageAdded="handleImageAdded"
              v-model="form.content"
            ></vue-editor>
            <div v-else class="html" v-html="form.content"></div>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import * as API from '@/api';
import { VueEditor } from 'vue2-editor';
import axios from 'axios';
export default {
  components: {
    VueEditor,
  },
  mounted() {
    if (this.$route.query.wendangid) {
      API.getgonggao({
        wendangid: this.$route.query.wendangid
      }).then(({ data }) => {
        this.form = data.data;
      });
    } else {
      API.getfawenhao().then(({ data }) => {
        this.form.wendangid = data.wendangid;
        this.upload.wendangid = this.form.wendangid;
      });
    }
  },
  data() {
    return {
      yulan: 0,
      baseurl: API.base,
      upload: {},
      form: {
        wendangid: '',
        biaoti: '',
        content: ' ',
        imagelist:[],
      }
    };
  },
  methods: {
    querenyulan() {
      this.yulan = 1 - this.yulan;
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      API.baocunimage(file).then(result => {
        let url = result.data.url;
        this.form.imagelist.push(url);
        Editor.insertEmbed(cursorLocation, 'image', url);
        resetUploader();
      });
    },
    querenfasong() {
      if (this.form.biaoti.length == 0) {
        this.$message({
          showClose: true,
          message: '公告标题不能为空',
          duration: 2000
        });
        return '';
      }
      API.fasonggonggao(this.form).then(({ data }) => {
        if (data.MSG == 'YES') {
          this.$message.success({
            showClose: true,
            message: '发送成功',
            duration: 2000
          });
          this.$router.push({
            path: '/gonggaoguanli'
          });
        } else {
          this.$message({
            message: '参数错误，请刷新后重试'
          });
        }
      });
    },
    guanbi() {
      this.$router.push({
            path: '/gonggaoguanli'
          });
    },
  }
};
</script>
<style lang="scss" scoped>
.xianshi {
  background: #efefef;
}
.gonggao {
  margin: 25px 0;
  // text-align: center;
  width: 80%;
  margin-left: 10%;
}
.fasong {
  text-align: center;
  margin-bottom: 30px;
}
.html {
  background: #efefef;
  width: 100%;
}
</style>



