<template>
  <div class="oa_main">
    <!--list-->
    <el-button type="primary" @click="xinjian">新建</el-button>
    <el-table border :data="qiefendataTable" stripe style="width: 100%" :default-sort="{prop: 'starttime', order: 'descending'}">
      <el-table-column sortable prop="biaoti" align="center" label="标题" show-overflow-tooltip min-width="200">
      </el-table-column>
      <el-table-column fixed="right" align="center" width="200" label="操作">
        <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">查看</el-button>
            <el-button size="mini" type="primary" @click="handleDele(scope.$index, scope.row)">删除</el-button>
</template>
      </el-table-column>
    </el-table>
    <div class="pailei">
      <el-pagination
        @current-change="handleCurrentChange"
        background
        :page-size="20"
        :pager-count="11"
        layout="prev, pager, next"
        :total="dataTable.length"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
  import * as API from '@/api';
  export default {
    components: {},
    mounted() {
      API.getgonggaoguanli({
        query: this.query
      }).then(({
        data
      }) => {
        this.dataTable = data.dataTable;
        this.qiefendataTable = this.dataTable.slice(0, 20);
      });
    },
    data() {
      return {
        query: '',
        qiefendataTable: [],
        dataTable: []
      };
    },
    methods: {
      xinjian() {
        this.$router.push({
          path: '/gonggao'
        });
      },
      handleCurrentChange(val) {
        this.qiefendataTable = this.dataTable.slice(20 * val - 20, val * 20);
      },
      handleEdit(index, row) {
        this.$router.push({
          path: '/gonggao',
          query: {
            wendangid: row.wendangid
          }
        });
      },
      handleDele(index, row) {
        API.deletegonggao({
          wendangid: row.wendangid
        }).then(({
          data
        }) => {
          this.dataTable = data.dataTable;
          this.qiefendataTable = this.dataTable.slice(0, 20);
        });
      }
    }
  };
</script>

<style lang="scss">
  .pailei {
    margin-top: 30px;
    text-align: center;
  }
</style>



