import axios from 'axios';

//设置全局axios默认值
axios.defaults.timeout = 5000; //5000的超时验证
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

//创建一个axios实例
const instance = axios.create();
instance.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

axios.interceptors.request.use = instance.interceptors.request.use;

//request拦截器
instance.interceptors.request.use(
    config => {
        //每次发送请求之前检测都vuex存有token,那么都要放在请求头发送给服务器
        if (localStorage.getItem('token')) {
            config.headers.Authorization = `token ${localStorage.getItem('token')}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);
//respone拦截器
instance.interceptors.response.use(
    response => {
        return response;
    },
    error => { //默认除了2XX之外的都是错误的，就会走这里
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    localStorage.removeItem('token'); //可能是token过期，清除它
                    router.replace({ //跳转到登录页面
                        path: 'login',
                        query: { redirect: router.currentRoute.fullPath } // 将跳转的路由path作为参数，登录成功后跳转到该路由
                    });
            }
        }
        return Promise.reject(error.response);
    }
);

//基地址
export const base = 'http://182.254.227.188';
export const port = ':6888/api/';
export const baseurl = base + port;
function fasonggonggao(data) {
    return instance.post(baseurl + 'fasonggonggao', data);
}
function getfawenhao(data) {
    return instance.post(baseurl + 'getfawenhao', data);
}
function deletegonggao(data) {
    return instance.post(baseurl + 'deletegonggao', data);
}
function getgonggaoguanli(data) {
    return instance.post(baseurl + 'getgonggaoguanli', data);
}
function baocunimage(data) {
    return instance.post(baseurl + 'baocunimage', data);
}
function getgonggao(data) {
    return instance.post(baseurl + 'getgonggao', data);
}
export {
    getfawenhao,
    deletegonggao,
    getgonggaoguanli,
    fasonggonggao,
    baocunimage,
    getgonggao,
}


